<template>
    <div class="catalog-item">
        <div class="catalog-item__image-wrap">
            <img class="catalog-item__image" :src=" require('../assets/images/' + product_data.image) " alt="img">
        </div>
        <p class="catalog-item__name">{{product_data.name}}</p>
        <p class="catalog-item__price">Price: {{product_data.price}} uah </p>
        <button 
            class="catalog-item__btn btn" 
            @click="addToCart">
            Add to cart
        </button>
    </div>
</template>

<script>
    export default {    
        name: 'CatalogItem',
        props: {
            product_data: {
                type: Object,
                default() {
                    return {}
                }
            }
        },
        methods: {
            addToCart() {
                this.$emit('addToCart',this.product_data)
            }
        }
    }
</script>
<style lang="scss">
    .catalog-item {
        flex-basis: 25%;
        box-shadow: 0 0 8px 0 $color-shadow;
        padding: $padding*2;
        margin-bottom: $margin*2;
        &__image-wrap {
            width: 100px;
            overflow: hidden;
            margin: 0 auto;
        }
        &__image {
            object-fit: cover;
            object-position: center;
            width: 100%;
            height: 100%;
        }
        &__btn {
            cursor: pointer;
        }
    }
</style>